<template>
  <div id="app">
    <!--<transition name="side">-->
      <!--<side-menu v-show="show" @hide="hideSide"></side-menu>-->
    <!--</transition>-->
    <router-view></router-view>
  </div>
</template>

<script>
//  import SideMenu from './components/SideMenu'
  import bus from './bus.js'
  export default {
    data(){
      return {
        show: false
      }
    },
    components: {
//          SideMenu
      },
//      当组件挂载到页面上之后
      mounted(){
        let self = this ;
        if(document.URL.match("share.wap.2boss") || document.URL.match("share.2boss")){
          self.apiBase = "http://ta.2boss.cn";
        }else{
          self.apiBase = "http://ta.qa.2boss.cn";
        };
//          bus.$on('sideShow',this.side)
      },
      methods: {}
  }
</script>

<style>
   .side-enter-active,.side-leave-active,
   .side-enter-active ul,.side-leave-active ul{
     transition:all .4s linear;
   }
   .side-enter,.side-leave-active{
     opacity: 0;
   }
   .side-enter ul,.side-leave-active ul{
     transform:translateX(-50%);
     opacity:0;
   }
</style>



